<template>
  <div class="box">
    <div class="shop_info_box">
      <div class="title_box" style="padding-left: 75px"><span>商品信息</span></div>
      <div>
        <el-image class="img" :src="evaluateData.evaluateUrl" fit="cover" />
        <p class="text">{{ evaluateData.prodName }}</p>
      </div>
    </div>
    <div class="evaluation_box">
      <div class="title_box"><span>评论内容</span></div>
      <div class="evaluatio_item">
        <p>买家评价：</p>
        <span v-if="evaluateData.type === 2" class="iconfont" style="color: #ff632d">&#xe607;</span>
        <span v-else-if="evaluateData.type === 1" class="iconfont" style="color: #ff632d">&#xe600;</span>
        <span v-else class="iconfont" style="color: #ff632d">&#xe625;</span>
        <span>{{ evaluateData.evaluate }}</span>
        <p class="time">{{ evaluateData.createTime }}</p>
        <div class="reply_box"><span>【卖家解释】{{ evaluateData.reply }}</span></div>
      </div>
      <div v-if="evaluateData.addEvaluate" class="evaluatio_item">
        <p>买家追评：</p>
        <span>{{ evaluateData.addEvaluate }}</span>
        <p class="time">{{ evaluateData.addEvaluateTime }}</p>
        <div v-if="evaluateData.addReply" class="reply_box"><span>【卖家解释】{{ evaluateData.addReply }}</span></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AdminOrderEvaluationContent',

  data () {
    return {
      evaluateData: '', // 评价信息
    }
  },

  methods: {
    // api------------------------------------------
    // 根据主键id查询
    apiOrderEvaluateFindById () {
      this.$http.post(`/woho/order/evaluate/findById/${this.$route.query.id}`)
        .then((res) => {
          this.evaluateData = res.data
        })
    }
  },

  created () {
    this.apiOrderEvaluateFindById()
  }
}
</script>

<style lang="scss" scoped>
  .box{
    width: 83.4%;
    display: flex;
    margin-left: 45px;
    .title_box{
      width: 100%;
      height: 44px;
      line-height: 44px;
      background-color: #e6ecf4;
    }
    .shop_info_box{
      width: 31%;
      .img{
        margin-top: 45px;
        width: 208px;
        height: 208px;
      }
      .text{
        font-size: 14px;
        width: 256px;
      }
    }
    .evaluation_box{
      width: 69%;
      .evaluatio_item{
        margin-top: 45px;
        font-size: 14px;
        .time{
          font-size: 12px;
          color: #999999;
        }
        .reply_box{
          width: 314px;
          height: 45px;
          line-height: 45px;
          background-color: #ecf3fb;
          padding-left: 15px;
          color: #f3ad33;
        }
      }
    }
  }
</style>
